.nav-item__link {
    text-decoration: none;
    color: inherit;
    font-weight: inherit;
    padding: 6px 8px;
}
.share {
    width: 400px;
    margin: 20px 40px 20px 0px;
    display: inline-block;
    text-align: left;
}

.share .share__buy-button {
    vertical-align: bottom;
    width: 100px;
    margin: 0 0 0 35px;
}

.share .share__sell-button {
    vertical-align: bottom;
    width: 100px;
    margin: 0 0 0 35px;
    background-color: #43a047;
}

.share .share__sell-button:hover {
    background-color: #388e3c;
}

.share .share__balance-container {
    display: -webkit-flex;
    display: flex;
    margin-top: 10px;
}

.share .share__balance-container__balance {
    display: inline-block;
    width: 63%;
}

.share .share__balance-container__cost {
    display: inline-block;
    width: 37%;
}

.portfolio {
    text-align: left;
}
.search {
    width: 100%;
    height: 50px;
    position: relative;
    padding-left: 72px;
    margin-left: 0;
    margin-right: 16px;
    border-radius: 4px;
    background-color: rgba(200, 200, 255, 0.15);
    box-shadow: 0 0 5px 0 rgba(0,0,0,0.2);
    box-sizing: border-box;
}

.search:hover {
    background-color: rgba(255,255,255, 0.25),
}
  
.search__search-icon {
    width: 72px;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    left: 0;
    position: absolute;
    -webkit-align-items: center;
            align-items: center;
    pointer-events: none;
    -webkit-justify-content: center;
            justify-content: center;
}

.search__input-root {
    color: inherit;
    width: 100%;
    height: 100%;
}

.search__input {
    padding-top: 2px;
    padding-right: 2px;
    padding-bottom: 2px;
    padding-left: 20px;
    width: 100%;
}
.manage-shares {
    box-sizing: border-box;
    padding: 20px;
    height: 100%;
    text-align: left;
}

.manage-shares .manage-shares__search-container {
    box-sizing: border-box;
    height: 100%;
    padding: 30px;
    overflow: auto;
}

.manage-shares .manage-shares__results-container {
    overflow: auto;
}

.manage-shares__results-container__title {
    text-align: left;
}
.manage-balance__form {
    width: 400px;
    margin: auto;
}

.manage-balance__form__method {
    text-align: left;
}
.manage-balance {
    text-align: center;
    padding: 20px;
}

.manage-balance__form-container {
    width: 50%;
    margin: auto;
}
.App {
  text-align: center;
  height: 100%;
}

.main {
  height: calc(100% - 64px);
}
html, body, #root {
  height: 100%;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

